#game_image1 {
  min-width: 18.25em;
  max-width: 18.25em;
  min-height: 18.875em;
  max-height: 18.875em;
}

.title_card {
  margin-right: 12px;
  margin-left: 12px;
}
.title_card--selected {
  margin-right: 12px;
  margin-left: 12px;
  border: 4px solid rgb(81, 1, 178);
  border-radius: 4px;
  box-shadow: 0px 10px 6px rgb(81, 1, 178);
}
#more {
  border-radius: 0px 8px 8px 0px;
  font-family: 'Bungee';
  color: rgb(8, 235, 182);
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0px;
  border-left: 1px solid white;
  min-width: 1.5em;
  max-width: 1.5em;
  height: 100%;
  background-image: linear-gradient(rgb(58, 57, 58), rgba(235, 226, 228, 0.856));
}
#more:hover {
  cursor: pointer;
}
#text {
  display: flex;
  flex-direction: column;
  word-break: break-word;
}

.show {
  min-width: 1238px;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 6.875em;
  width: 18.25em;
  border: 2px solid white;
  font-family: 'Bungee Hairline';
  color: white;
  font-size: 26px;
  margin-bottom: 24px;
  height: 240px !important;
  display: flex;
  align-items: flex-start;
  background-color: rgb(24, 23, 23);
  transition: 2s;
  font-weight: 800;
}
#overview {
  font-family: 'Bungee';
  color: white;
  font-size: 45px;
  margin: 0px;
}

#desc {
  margin: 0px;
}
@media (max-width :360px) {
  #game_image1 {
    max-width: 100%;
    min-width: 100%;
    max-height: 100%;
    min-height: 100%;
  }
  
  .title_card {
    flex: 0 0 auto;
    width: 90%;
    height: 52em;
    margin: 0em 1em 2em 1em;
  }
  .title_card--selected {
    flex: 0 0 auto;
    width: 90%;
    height: 52em;
    margin: 0em 1em 2em 1em;
    border: 4px solid rgb(81, 1, 178);
    border-radius: 4px;
    box-shadow: 0px 10px 6px rgb(81, 1, 178);
  }
  #overview {
    font-family: 'Bungee';
    color: white;
    font-size: 1.8em;
    margin: 0px;
  }
  .show {
    min-width: 90%;
    border-radius: 8px;
    flex-direction: row;
    justify-content: space-between;
    min-height: 14em; 
    border: 2px solid white;
    font-family: 'Bungee Hairline';
    color: white;
    font-size: 2em;
    margin-bottom: 24px;
    margin-left: 1em;
    margin-right: 1em;
    display: flex;
    align-items: flex-start;
    background-color: rgb(24, 23, 23);
    transition: 2s;
    font-weight: 800;
  }
}
